media (max-width: 540px){.room-details__content{padding:70px 0}}.room-details__content .room-details__info{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:33px}@media (max-width: 930px){.room-details__content .room-details__info{display:flex;flex-direction:column;align-items:center}}.room-details__content .room-details__info .room-details__feature{max-width:290px}@media (max-width: 930px){.room-details__content .room-details__info .room-details__feature{margin-bottom:30px}}.room-details__content .room-details__info .room-details__feature .feature__wrapper .feature:last-child{margin-bottom:0;padding-bottom:0;border-bottom:none}.room-details__content .room-details__info .room-details__impressions{max-width:321px}@media (max-width: 930px){.room-details__content .room-details__info .room-details__impressions{margin-bottom:30px;width:320px}}@media (max-width: 400px){.room-details__content .room-details__info .room-details__impressions__header{padding:0 20px}}.room-details__content .room-details__info .room-details__reviews{grid-column:1/3;margin-right:14px}@media (max-width: 930px){.room-details__content .room-details__info .room-details__reviews{grid-column:auto;margin-right:0;margin-bottom:30px}}@media (max-width: 540px){.room-details__content .room-details__info .room-details__reviews{padding:0 20px}}.room-details__content .room-details__info .room-details__reviews__header{display:flex;justify-content:space-between}@media (max-width: 430px){.room-details__content .room-details__info .room-details__reviews__header{flex-direction:column}}.room-details__content .room-details__info .room-details__reviews__header .reviews__number{font-size:14px;color:rgba(31,32,65,0.5)}@media (max-width: 430px){.room-details__content .room-details__info .room-details__reviews__header .reviews__number{align-self:flex-end}}.room-details__content .room-details__info .room-details__reviews .comment{margin-bottom:20px}.room-details__content .room-details__info .room-details__reviews .comment:last-child{margin-bottom:0}@media (max-width: 930px){.room-details__content .room-details__info .room-details__bullet-list{margin-bottom:30px}}.room-details__content .room-details__info .room-details__bullet-list__header{margin-left:20px;margin-bottom:16px}.room-details__content .room-details__info .room-details__bullet-list .bullet{margin-bottom:10px}.room-details__content .room-details__info .room-details__cancellation{max-width:340px}@media (max-width: 400px){.room-details__content .room-details__info .room-details__cancellation{max-width:auto;padding:0 20px}}.room-details__content .room-details__info .room-details__cancellation__header{margin-bottom:16px}.room-details__content .room-details__card{width:380px}@media (max-width: 540px){.room-details__content .room-details__card{width:320px}}.room-details__content h2{margin-bottom:20px}
