body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.header{background-color:#fff;box-shadow:0px 10px 20px rgba(31,32,65,0.05)}.header__wrapper{max-width:1920px;display:flex;justify-content:center;align-items:center;padding:15px 9.72%;margin:0 auto}@media (max-width: 1200px){.header__wrapper{padding:15px 5%}}.header__wrapper .logo{margin-right:auto}@media (max-width: 1020px){.header__wrapper .logo{margin-right:20px}}@media (max-width: 440px){.header__wrapper .logo{display:none}}.header__wrapper .menu{margin-right:30px;position:relative}@media (max-width: 1020px){.header__wrapper .menu .nav-menu{display:none}}.header__wrapper .menu .menu-active{position:absolute;top:50px;display:flex;flex-direction:column;background:#fff;box-shadow:0px 0px 25px rgba(0,0,0,0.2);border-radius:4px;padding:10px 20px;z-index:3}.header__wrapper .menu .menu-active .nav-menu__item{margin-bottom:10px}.header__wrapper .menu .menu-active .nav-menu__item .submenu-active{left:100%;top:0}.header__wrapper .burger-menu{display:none;cursor:pointer;font-size:30px;-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-color:#bc9cff}@media (max-width: 1020px){.header__wrapper .burger-menu{display:block}}.header__wrapper .burger-menu::after{content:"menu"}.header__wrapper .cross-icon::after{content:"close"}.header__wrapper .login-name{border-left:1px solid rgba(31,32,65,0.1);padding-left:35px}.header__wrapper .buttons{display:flex}@media (max-width: 540px){.header__wrapper .buttons{display:flex;flex-direction:column;align-items:center}}.header__wrapper .buttons .button{padding:10px 20px 9px 19px}.header__wrapper .buttons .button_theme_border{padding:8px 18px 7px;margin-right:20px}@media (max-width: 540px){.header__wrapper .buttons .button_theme_border{margin-right:0;margin-bottom:10px}}
