body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.login{border:1px solid rgba(0,0,0,0.12);box-sizing:border-box;box-shadow:0px 0px 25px rgba(0,0,0,0.2);border-radius:4px;background:#ffffff;padding:40px 30px 31px;margin-bottom:40px}.login__header{margin-bottom:19px}.login .field__wrapper{margin-bottom:10px}.login .button_size_long{margin-top:20px}.login__foot{margin-top:30px;display:flex;align-items:center;justify-content:space-between}.login__foot .login__note{color:#000000}
