body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.sample{max-width:270px;background:#ffffff;box-shadow:0px 10px 20px rgba(31,32,65,0.05);border-radius:4px;user-select:none;cursor:pointer}.sample-image{position:relative;display:flex;width:270px;height:151.38px;border-radius:4px 4px 0 0;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.sample__container{position:relative}.sample .room-img{border-radius:4px 4px 0 0}.sample .image-ellipses__wrapper{position:absolute;display:flex;align-self:flex-end;justify-content:space-between;width:42px;right:5%;bottom:10%}.sample .image-ellipses__wrapper .image-ellipse{width:7.5px;height:7.5px;border:1px solid #ffffff;border-radius:50%;box-sizing:border-box;box-shadow:0px 5px 5px rgba(31,32,65,0.3)}.sample .image-ellipses__wrapper .elipse-filled{background:#fff}.sample .image-room{display:none}.sample .image-room.image-show{display:block}.sample .image__arrow__left{display:flex;align-items:center;justify-content:center;left:0;position:absolute;width:44px;height:100%}.sample .image__arrow__right{display:flex;right:0;align-items:center;justify-content:center;transform:rotate(180deg);position:absolute;width:44px;height:100%}.sample__info{padding:20px 20px 10px}.sample__info .sample__price{display:flex;align-items:baseline;padding-bottom:10px;margin-bottom:8px;border-bottom:1px solid rgba(31,32,65,0.1)}.sample__info .price__numeration{font-family:'open-sans', sans-serif;font-weight:bold;font-size:14px;line-height:17px;color:#1f2041;margin-right:5px}.sample__info .price__number{font-family:'open-sans', sans-serif;font-weight:bold;font-size:19px;line-height:24px;color:#1f2041;margin-right:5px}.sample__info .price__luxury{font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#bc9cff}.sample__info .price__cost,.sample__info .reviews__number{font-weight:bold;font-size:14px;color:rgba(31,32,65,0.5);margin-left:auto;margin-right:5px}.sample__info .price__inday{font-size:12px;line-height:18px;color:rgba(31,32,65,0.5)}.sample__info .sample__reviews{display:flex}.sample__info .reviews__text{font-size:14px;color:rgba(31,32,65,0.5)}.sample-image .sample__image-prev,.sample-image .sample__image-next{display:none}.sample-image:hover .sample__image-prev,.sample-image:hover .sample__image-next{display:block;font-size:24px;color:white}.sample-image:hover .image__arrow__left,.sample-image:hover .image__arrow__right{background:linear-gradient(90deg, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 99.99%, rgba(255,255,255,0) 100%);cursor:pointer}
