.js-diagram .diagram{display:flex}.js-diagram .diagram__wrap,.js-diagram .diagram__legend{align-self:center}.js-diagram .diagram .donut__sector{cursor:pointer}.js-diagram .diagram .donut__label{font-size:16px;fill:#000;-moz-transform:translateY(0.25em);-ms-transform:translateY(0.25em);-webkit-transform:translateY(0.25em);transform:translateY(0.25em)}.js-diagram .diagram .donut__value{font-size:0.4em;font-weight:bold;line-height:1;text-anchor:middle;-moz-transform:translateY(-0.25em);-ms-transform:translateY(-0.25em);-webkit-transform:translateY(-0.25em);transform:translateY(-0.25em)}.js-diagram .diagram .donut__unit{font-size:0.2em;font-weight:bold;text-transform:uppercase;text-anchor:middle;-moz-transform:translateY(0.7em);-ms-transform:translateY(0.7em);-webkit-transform:translateY(0.7em);transform:translateY(0.7em)}.js-diagram .diagram__legend{padding-right:5px;padding-top:2px}.js-diagram .diagram__legend .legend-item{display:flex;align-items:center;cursor:pointer}.js-diagram .diagram__legend .legend-item__circle{width:10px;height:10px;border-radius:50%;margin-right:5px}.js-diagram .diagram__legend .legend-item .circle_theme_satisfactorily{background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.js-diagram .diagram__legend .legend-item .circle_theme_good{background:linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%)}.js-diagram .diagram__legend .legend-item .circle_theme_perfectly{background:linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%)}.js-diagram .diagram__legend .legend-item .circle_theme_disappointed{background:linear-gradient(180deg, #919191 0%, #3d4975 100%)}
