body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.checkbox-list__header{position:relative;line-height:24px;margin-bottom:14px;cursor:pointer}.checkbox-list__header .expand-icon{position:absolute;right:5%;font-size:24px;line-height:24px;color:rgba(31,32,65,0.5)}.checkbox-list__header .expand-icon_open{transform:rotate(180deg)}.checkbox-list__menu{display:none}.checkbox-list__menu .checkbox{margin-bottom:12px}.checkbox-list__menu_open{display:block}
