a (max-width: 1020px){.footer__subscription{text-align:center}}.footer__subscription-title{font-size:12px;font-weight:bold;text-transform:uppercase;color:#1f2041;margin-bottom:13px}.footer__subscription-text{max-width:260px;margin-bottom:23px}.footer__subscription .field .arrow__icon{top:10px}.footer__line{border-top:1px solid #e5e5e5}.footer__copyright{max-width:1920px;margin:0 auto;display:flex;justify-content:space-between;padding:23px 9.72% 19px;box-shadow:0px 10px 20px rgba(31,32,65,0.05)}@media (max-width: 1200px){.footer__copyright{padding:23px 5%}}@media (max-width: 560px){.footer__copyright{flex-direction:column;align-items:center}}.footer__copyright .copyright__text{text-align:center}.footer__copyright .copyright__social{display:flex}@media (max-width: 560px){.footer__copyright .copyright__social{margin-top:10px}}.footer__copyright .copyright__social .social{margin-right:24px}.footer__copyright .copyright__social .social:last-child{margin-right:0}
