.search-room{max-width:1920px;margin:0 auto;display:grid;grid-template-columns:1fr 3fr;padding:26px 9.72% 73px}@media (max-width: 1024px){.search-room{grid-template-columns:1fr 2fr;padding:26px 5% 73px}}@media (max-width: 824px){.search-room{grid-template-columns:none;justify-items:center}}.search-room__sidebar{max-width:266px}.search-room__sidebar .sidebar__item{margin-bottom:20px}.search-room__sidebar .sidebar__item .checkbox{max-width:240px}.search-room__sidebar .sidebar__item .dropdown-guests__title{margin-bottom:4px}.search-room__sidebar .sidebar__item .slider,.search-room__sidebar .sidebar__item .checkbox-buttons{margin-top:30px}.search-room__sidebar .sidebar__item .rich-checkbox__header{margin-top:32px;margin-bottom:15px}.search-room__sidebar .sidebar__item .rich-checkbox{margin-bottom:12px}.search-room__sidebar .sidebar__item .dropdown-rooms__title{margin-bottom:4px;margin-top:28px}.search-room__sidebar .sidebar__item .checkbox-list{margin-top:25px}.search-room__samples{display:grid;margin-left:36px;margin-top:5px}@media (max-width: 824px){.search-room__samples{margin-left:0}}.search-room__samples__header{margin-bottom:20px}.search-room__samples .samples__wrapper{display:grid;justify-items:center;grid-template-columns:repeat(auto-fill, minmax(270px, 1fr));row-gap:20px;column-gap:12px;margin-bottom:41px}.search-room__samples .pagination{justify-self:center;margin-right:15px}
