body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.iqdropdown{display:flex;align-items:center;position:relative}.iqdropdown__icon{position:absolute;right:10px;font-size:24px;line-height:1px;color:rgba(31,32,65,0.5);cursor:pointer}.iqdropdown .icon_active{transform:rotate(180deg)}.iqdropdown__selection{font-family:'montserrat', sans-serif;color:rgba(31,32,65,0.75);font-size:14px;letter-spacing:0.02rem;box-sizing:border-box;border-radius:4px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);width:100%;height:auto;line-height:18px;padding:12px 15px;outline:none}.iqdropdown__selection::after{display:none}.iqdropdown__selection:focus,.iqdropdown__selection:hover{border:1px solid rgba(31,32,65,0.5);color:rgba(31,32,65,0.75)}.iqdropdown::placeholder{color:rgba(31,32,65,0.25);font-size:14px;line-height:18px}.iqdropdown__menu{visibility:hidden;opacity:0;position:absolute;background:#fff;left:0;z-index:99}.iqdropdown__menu-option{display:flex;justify-content:space-between;align-items:center;padding:0px 5px 5px 14px}.iqdropdown__item{font-size:12px;font-weight:800;color:#1f2041;text-transform:uppercase}.iqdropdown__controls{display:flex}.iqdropdown__controls-decrement,.iqdropdown__controls-increment{border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:22px;height:auto;padding:7px}.iqdropdown__controls-count{width:30px;font-size:12px;font-family:'montserrat', sans-serif;font-weight:bold;padding:0;border:none;text-align:center;outline:none}.iqdropdown._active .iqdropdown__selection{border:1px solid rgba(31,32,65,0.5);border-radius:4px 4px 0 0}.iqdropdown._active .iqdropdown__menu{width:100%;padding-top:5px;border-radius:0 0 4px 4px;border:1px solid rgba(31,32,65,0.5);visibility:visible;opacity:1;transition:opacity 0.15s ease-in;top:42px;box-sizing:border-box}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons{display:flex;padding:13px 7px 12px 13px;justify-content:space-between}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button{font-family:'montserrat', sans-serif;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;border:none;background:none;cursor:pointer}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button,.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button:active,.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button:focus{outline:none}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button-apply{color:#bc9cff;margin-left:auto}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button-clear{visibility:hidden;color:rgba(31,32,65,0.5)}.iqdropdown._active .iqdropdown__menu .iqdropdown__buttons .iqdropdown__button-clear._active{visibility:visible}.iqdropdown._active .iqdropdown__menu .controls-button{display:flex;justify-content:center;align-items:center;width:30px;height:30px;border:1px solid rgba(31,32,65,0.5);border-radius:50%;user-select:none;cursor:pointer}.iqdropdown._active .iqdropdown__menu .controls-button._disabled{opacity:0.5;cursor:default}
