body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.booking{border:1px solid rgba(0,0,0,0.12);box-sizing:border-box;box-shadow:0px 0px 25px rgba(0,0,0,0.2);border-radius:4px;background:#ffffff;padding:40px 30px 29px;margin-bottom:40px}.booking__header{display:flex;align-items:baseline;margin-bottom:15px}.booking__header-numeration{font-family:'open-sans', sans-serif;font-weight:bold;font-size:14px;line-height:17px;color:#1f2041;margin-right:5px}.booking__header-number{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-right:5px}.booking__header-luxury{font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#bc9cff}.booking__header-cost{font-weight:bold;font-size:14px;line-height:18px;color:rgba(31,32,65,0.5);margin-left:auto;margin-right:5px}.booking__header-inday{font-size:12px;line-height:18px;color:rgba(31,32,65,0.5)}.booking__date-range{display:flex;margin-bottom:19px}.booking__date-range .field:first-child{margin-right:20px}.booking__guests{margin-bottom:21px}.booking__guests-title{margin-bottom:4px}.booking__price{margin-bottom:30px}.booking__price .price-item{display:flex;align-items:flex-start;margin-bottom:16px;line-height:18px}.booking__price .price-item__name{max-width:214px}.booking__price .price-item__info{font-size:26px;line-height:18px;color:rgba(31,32,65,0.25);transform:rotate(180deg)}.booking__price .price-item__cost{margin-left:auto}.booking__total{display:flex;justify-content:space-between;align-items:baseline;font-family:'open-sans', sans-serif;font-weight:bold;font-size:19px;color:#1f2041;margin-bottom:20px}.booking__total-title{margin-right:5px}.booking__total-points{flex:1;border-bottom:1px dotted rgba(31,32,65,0.25)}.booking__total-cost{margin-left:5px}
