body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.menu{display:flex;align-items:center}.menu .nav-menu{display:flex}.menu .nav-menu__item{position:relative;margin-right:27px}.menu .nav-menu__item .nav-menu__item-link,.menu .nav-menu__item .nav-menu__submenu-item{display:flex;color:rgba(31,32,65,0.5);align-items:center;cursor:pointer}.menu .nav-menu__item .expand-more::after{content:'expand_more';font-family:'material-icons';font-size:24px;cursor:pointer}.menu .nav-menu__item .more-active::after{transform:rotate(180deg)}.menu .nav-menu__item .item_theme_bold{font-weight:bold}.menu .nav-menu__item .js-nav-menu__submenu{display:none}.menu .nav-menu__item .submenu-active{display:block;position:absolute;background-color:white;border-radius:4px;box-shadow:5px 5px 5px rgba(0,0,0,0.2);padding:10px 15px}.menu .nav-menu__item .submenu-active .nav-menu__submenu-item{margin-top:10px}.menu .nav-menu__item .submenu-active .nav-menu__submenu-item:first-child{margin-top:0}.menu .nav-menu .nav-menu__item:last-child{margin-right:0}
