body{margin:0;font-family:'montserrat', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', sans-serif;font-weight:bold;font-size:24px;line-height:30px;letter-spacing:1.2px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;letter-spacing:0.02px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.toggle__label{padding-left:50px;position:relative;cursor:pointer;user-select:none}.toggle__label .toggle__input{display:none}.toggle__label .toggle__marker{position:absolute;top:0;left:0;width:40px;height:20px;border:1px solid rgba(31,32,65,0.25);border-radius:10px;transition:0.3s}.toggle__label .toggle__marker::after{display:block;position:absolute;content:'';width:12px;height:12px;top:4px;left:4px;border-radius:50%;background-color:rgba(31,32,65,0.25);transition:0.3s}.toggle__label .toggle__input:checked ~ .toggle__marker::after{transform:translateX(20px);background-image:linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)}.toggle__label .toggle__input:checked ~ .toggle__marker{border-color:#bc9cff}
